@import url(http://cdn.jsdelivr.net/font-nanum/1.0/nanumbarungothic/nanumbarungothic.css);

*{
  /* border:1px solid red; */
  list-style: none;
  margin:0;
  padding:0;
  font-family: 'Nanum Barun Gothic';
  font-weight: 400;
}

div,input,button,ul{
  border-radius: 5px;
}

header{
  margin-top: 7px;
}

.vertical-buttons input, .vertical-buttons button{
  display: block;
  width: 90%;
  margin: 1px auto;
  padding: 3px;
}

.vertical-buttons button{
  margin-top:3px;
  background-color: rgb(218, 218, 218);
}

.member{
  width: 90%;
  margin:2px auto !important;
  overflow: hidden;
  /* float: left; */
}

.delete-btn{
  width:10% !important;
  padding:0 !important;
  margin:0 !important;
  float: right;
  font-weight: 500;
  color:red;

}

.team{
  border: 1px solid rgb(77, 75, 75);
  width:90%;
  margin: 5px auto;
  /* margin-top: 5px; */
}

.team li{
  padding: 1px 0;
}

.top-boarder{
  border-top: 1px solid black;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



